.wrapBanner {
  margin-bottom: 80px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.title {
  font-weight: 600;
  font-size: 28px;
  line-height: 114%;
  text-align: center;
  color: #1d1e21;
  margin: 0;
  margin-bottom: 14px;
}

.text {
  font-size: 14px;
  line-height: 129%;
  text-align: center;
  color: #93939a;
  margin: 0;
  margin-bottom: 40px;
}

.containerItem {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  gap: 20px;
  width: 100%;
}

@media (min-width: 768px) and (max-width: 1439px) {
  .wrapBanner {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 768px) {
  .wrapBanner {
    margin-bottom: 120px;
  }
  .title {
    font-size: 40px;
    line-height: 120%;
  }
  .text {
    font-size: 16px;
    line-height: 125%;
    margin-bottom: 64px;
  }
}

@media (min-width: 1440px) {
  .wrapBanner {
    padding-left: 96px;
    padding-right: 96px;
  }
}
